@import"../../global.scss";
.experience{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include mobile{
            justify-content:space-around;
    }
    h1{
        font-size: 50px;
        span{
            color:crimson
        }
        
        @include mobile{
            font-size: 20px;
            margin: 0;
            padding: 0;
        }
    }
    .container{
        width: 100%;
        height:80%;
        display: flex;
        align-items:center ;
        justify-content: center;
        @include mobile{
            flex-direction: column;
            height: 100%; 
                
           


        }

        .card{
            width:250px;
            height:70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            margin:0 30px ;
            @include mobile{
                height: 180px;
                margin: 10px 0;
               
            }
            &:hover{
                transform: scale(1.1);
                transition: all 1s ease;
                
            }
            
            .top{
                display: flex;
                align-items: center;
                justify-content: center;
                img{
                    &.left{
                        height: 25px;
                        display: none;
                    }
                    &.user{
                        height:90px;
                        width:120px;
                        border-radius: 25%;
                        object-fit: cover;
                        margin: 0 30px;
                        @include mobile{
                                width: 50px;
                                height: 40px;
                        }
                    }
                }
            }
            .center{
                padding: 10px;
                border-radius: 10px;
                background-color: rgb(250,244,245);
                @include mobile{
                    font-size: 12px;
                    padding: 5px;
                }
            }
            .bottom{
                display:flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                h3{
                    margin-bottom: 5px;
                    @include mobile{
                        font-size: 14px;
                    }
                }
            
            }
        }


    }

}